	                    _paragraph2.default,
	                    null,
	                    'When Epic Games introduced their new type of license for the Unreal Engine 4 (the so-called EaaS, i.e. \u201Cengine as a service\u201D), it was only a matter of time for me to start testing and using the engine. To be honest, I had certain difficulties understanding some basic concepts of various things that make up the engine editor. Most importantly, the blueprints. It is indeed a very powerful system, and judging by various tutorials that float around the Internet (including the official ones), this system can do almost anything that you previously needed to write code for.'
