import React from 'react';
import Code from './../shared/code';
import Comments from './../shared/comments';
import PageTitle from './../shared/page-title';
import Paragraph from './../shared/paragraph';
import InlineCode from './../shared/inline-code';

export const date = '05 May 2013';
export const title = 'Unity3D tip: edit arrays of custom objects in object inspector';

export default class Page extends React.Component {
    render() {
        return (
            <div>
                <PageTitle text={title} date={date}/>

                <Paragraph>
                    Say I have the following component:
                </Paragraph>

                <Code lang="csharp">
{`public class Test: MonoBehaviour
{
    public Vector3[] PivotPoints;
}`}
                </Code>

                <Paragraph>
                    It’s OK to edit the <InlineCode>PivotPoints</InlineCode> field in object inspector, because
                    Unity natively understands <InlineCode>Vector3</InlineCode> struct and displays it properly.
                    Things get more complicated when I add field representing an array of custom objects:
                </Paragraph>

                <Code lang="csharp">
{`class NodeSetup
{
    public Vector3 Position;
    public Vector3 Weight;
}

// ...

public class Test: MonoBehaviour
{
    public Vector3[] PivotPoints;
    public NodeSetup[] NodeSetups;
}`}
                </Code>

                <Paragraph>
                    Unity ignores field <InlineCode>NodeSetups</InlineCode>. First I thought I should write a
                    custom editor in order to edit such fields. Fortunately the problem has
                    simple solution – just mark your custom class with <InlineCode>Serializable</InlineCode> attribute:
                </Paragraph>

                <Code lang="csharp">
{`[Serializable]
class NodeSetup
{ ... }`}
                </Code>

                <Comments/>
            </div>
        );
    }
}


