import React from 'react';
import Code from './../shared/code';
import Comments from './../shared/comments';
import PageTitle from './../shared/page-title';
import SectionTitle from './../shared/section-title';
import Paragraph from './../shared/paragraph';
import Link from './../shared/link';
import InlineCode from './../shared/inline-code';
import ArticleImages from './../shared/article-images';

import img1 from './../img/clion/logo.png';
import img2 from './../img/clion/project.png';

export const date = '19 Oct 2014';
export const title = 'Building multiple linked binaries in CLion on Mac OS X';

export default class Page extends React.Component {
    render() {
        return (
            <div>
                <PageTitle text={title} date={date}/>

                <ArticleImages images={[img1]}/>

                <Paragraph>
                    <Link to="https://www.jetbrains.com/clion/">CLion</Link> is a new C/C++ IDE from JetBrains, the creators of
                    ReSharper, IntelliJ IDEA and many other great products for
                    software developers. CLion is claimed to support a full-featured
                    refactoring workflow for C++, similar to how it is already
                    implemented in other JetBrains products. At this moment the IDE
                    is available in early access mode, which means that we can download
                    and use it freely, keeping in mind that the product is not stable yet,
                    so bugs are possible.
                </Paragraph>

                <Paragraph>
                    I decided to move one of my projects from XCode to CLion.
                    The refactoring capabilities of XCode for C++ are far from
                    being good (in fact, there is no refactoring support for C++),
                    and after using ReSharper for my C# projects, I simply don’t
                    understand how to work without “Rename”, “Find usages”,
                    “Generate missing members”, smart highlighting and a plenty of other super-useful features.
                </Paragraph>

                <Paragraph>
                    CLion uses CMake to describe and manage its projects (at least yet).
                    If you’re not very familiar with CMake (like me), creating and building
                    anything more complex than the default Hello world will be sort of
                    a nightmare. In this post I’ll share my experience in building a
                    static library and a project that uses this library in CLion,
                    hoping that this will save a lot of googling time for anyone having the similar task.
                </Paragraph>

                <Paragraph>
                    For example, we want to build a static library named solo
                    and a test project Test1 that links against it. My project structure looks like this:
                </Paragraph>

                <ArticleImages images={[img2]}/>

                <Paragraph>
                    The <InlineCode>src</InlineCode> folder contains the library source
                    files and the <InlineCode>include</InlineCode> folder contains the interface headers.
                    The <InlineCode>Test1</InlineCode> project resides inside the <InlineCode>tests/test1</InlineCode> folder.
                    I’m using a single <InlineCode>CMakeLists.txt</InlineCode> for everything for the sake of
                    simplicity. As the project grows bigger, you might consider
                    splitting it into several <InlineCode>CMakeLists’</InlineCode> to make it modular and easier to manage.
                </Paragraph>

                <Paragraph>
                    Lets examine the <InlineCode>CMakeLists.txt</InlineCode> file. The first two lines are added by CLion by default:
                </Paragraph>

                <Code lang="cmake">
{`cmake_minimum_required(VERSION 2.8.4)
project(solo)`}
                </Code>

                <Paragraph>
                    They state that we want CMake version to be at least 2.8.4, and our project is called <InlineCode>solo</InlineCode>.
                </Paragraph>

                <Paragraph>
                    Here comes the first interesting part: we tell CLion where to save the output binaries.
                    By default, they are put into some wierd location
                    like <InlineCode>{`/Users/<username>/Library/Caches/clion10/cmake/generated/707a014b/707a014b/Debug`}</InlineCode>.
                    Here we want to store them in some more conventional place like the build folder inside the project folder:
                </Paragraph>

                <Code lang="cmake">
{`set(EXECUTABLE_OUTPUT_PATH "$\{PROJECT_SOURCE_DIR\}/build/")
set(LIBRARY_OUTPUT_PATH "$\{PROJECT_SOURCE_DIR\}/build/")`}
                </Code>

                <Paragraph>
                    The next line tells CMake to pass specific flags to the Clang compiler:
                </Paragraph>

                <Code lang="cmake">
{`add_compile_options(-std=c++0x)`}
                </Code>

                <Paragraph>
                    Our library needs some headers to compile, so we tell CMake to search them in a number of directores:
                </Paragraph>

                <Code lang="cmake">
{`include_directories(
    $\{PROJECT_SOURCE_DIR\}/include
    $\{PROJECT_SOURCE_DIR\}/external/SDL/2.0.3/include)`}
                </Code>

                <Paragraph>
                    Here the <InlineCode>{`$\{PROJECT_SOURCE_DIR\}`}</InlineCode> refers to the root project folder.
                </Paragraph>

                <Paragraph>
                    We also need to specify where to look for the external libraries when linking our library against them:
                </Paragraph>

                <Code lang="cmake">
{`link_directories($\{PROJECT_SOURCE_DIR\}/external/SDL/2.0.3)`}
                </Code>

                <Paragraph>
                    Next we tell CMake to find all <InlineCode>*.cpp</InlineCode> files inside the src folder and put them into
                    the <InlineCode>sources</InlineCode> variable:
                </Paragraph>

                <Code lang="cmake">
{`file(GLOB sources "src/*.cpp")`}
                </Code>

                <Paragraph>
                    In the next chunk of code we tell CMake to find some frameworks that we’re going
                    to be using, and save their paths to the frameworks variable:
                </Paragraph>

                <Code lang="cmake">
{`find_library(carbon_lib Carbon) # we look for the Carbon framework and use carbon_lib as an alias for it
find_library(iokit_lib IOKit)
find_library(forcefeedback_lib ForceFeedback)
find_library(cocoa_lib Cocoa)
find_library(audiounit_lib AudioUnit)
find_library(coreaudio_lib CoreAudio)
find_library(opengl_lib OpenGL)
find_library(corefoundation_lib CoreFoundation)

set(frameworks
    $\{carbon_lib\}
    $\{iokit_lib\}
    $\{forcefeedback_lib\}
    $\{cocoa_lib\}
    $\{audiounit_lib\}
    $\{coreaudio_lib\}
    $\{opengl_lib\}
    $\{corefoundation_lib\})`}
                </Code>

                <Paragraph>
                    Finally, we say that we want to build a static library from the given sources
                    and link it against the SDL2 library and the frameworks that we found before:
                </Paragraph>

                <Code lang="cmake">
{`add_library(solo STATIC $\{sources\})
target_link_libraries(solo SDL2 $\{frameworks\})`}
                </Code>

                <Paragraph>
                    Adding the second binary (the <InlineCode>Test1</InlineCode> executable)
                    is as easy as it is. We tell CMake to build it from
                    the <InlineCode>Main.cpp</InlineCode> file and link agains the <InlineCode>solo</InlineCode> library:
                </Paragraph>

                <Code lang="cmake">
{`add_executable(Test1 tests/test1/Main.cpp)
target_link_libraries(Test1 LINK_PUBLIC solo)`}
                </Code>

                <Paragraph>
                    Now if you hit <InlineCode>Cmd + F9</InlineCode> (Build), you should see
                    two new files inside the build folder: <InlineCode>Test1</InlineCode> and <InlineCode>libsolo.a</InlineCode>.
                </Paragraph>

                <Paragraph>
                    The whole <InlineCode>CMakeLists.txt</InlineCode> file:
                </Paragraph>

                <Code lang="cmake">
{`cmake_minimum_required(VERSION 2.8.4)
project(solo)

set(EXECUTABLE_OUTPUT_PATH "$\{PROJECT_SOURCE_DIR\}/build/")
set(LIBRARY_OUTPUT_PATH "$\{PROJECT_SOURCE_DIR\}/build/")

add_compile_options(-std=c++0x)

include_directories(
    $\{PROJECT_SOURCE_DIR\}/include
    $\{PROJECT_SOURCE_DIR\}/external/SDL/2.0.3/include)

link_directories($\{PROJECT_SOURCE_DIR\}/external/SDL/2.0.3)

# Engine

file(GLOB sources "src/*.cpp")

find_library(carbon_lib Carbon)
find_library(iokit_lib IOKit)
find_library(forcefeedback_lib ForceFeedback)
find_library(cocoa_lib Cocoa)
find_library(audiounit_lib AudioUnit)
find_library(coreaudio_lib CoreAudio)
find_library(opengl_lib OpenGL)
find_library(corefoundation_lib CoreFoundation)

set(frameworks
    $\{carbon_lib\}
    $\{iokit_lib\}
    $\{forcefeedback_lib\}
    $\{cocoa_lib\}
    $\{audiounit_lib\}
    $\{coreaudio_lib\}
    $\{opengl_lib\}
    $\{corefoundation_lib\})

add_library(solo STATIC $\{sources\})
target_link_libraries(solo SDL2 $\{frameworks\})

# Tests
add_executable(Test1 tests/test1/Main.cpp)
target_link_libraries(Test1 LINK_PUBLIC solo)`}
                </Code>

                <SectionTitle text="Conclusion"/>

                <Paragraph>
                    This article gives a recipe for writing a simple CMake file
                    for building two linked binaries in CLion. Keep in mind that I’m not
                        an expert in CMake, neither in Clion, so the given file might not
                    be the best that you can make to get the job done. However, it gives
                    a good start for working with CLion, which, as I have noticed,
                    is not well covered in the online tutorials yet.
                </Paragraph>

                <Comments/>
            </div>
        );
    }
}


