import React from 'react';
import injectSheet from 'react-jss';
import PageTitle from './../shared/page-title';
import Paragraph from './../shared/paragraph';
import Link from './../shared/link';

import * as ChaiscriptRecipesPage from './chaiscript-recipes';
import * as LuaIntfPage from './lua-intf';
import * as NsfDevlog1Page from './nsf-devlog';
import * as NsfDevlog2Page from './nsf-devlog2';
import * as ClionBinariesPage from './clion-multiple-binaries';
import * as UnityPickAndDragPage from './unity3d-pick-and-drag';
import * as UnrealPlatformsPage from './unreal-platforms';
import * as WindowsMainPage from './windows-main';
import * as NetherDevlogPage from './nether-devlog';
import * as UnityArrayOfObjectsPage from './unity3d-array-of-custom-objects';
import * as FreeGlutPage from './freeglut-fixed-window';
import * as OddCppPage from './odd-cpp';
import * as UnityCameraPage from './unity3d-spectator-camera';

// TODO add these properly
// <Item link="/odd-cpp" title="Something you probably didn’t know about C/C++" date="09 Mar 2013"/>
// <Item link="/unity3d-spectator-camera" title="Unity3d tip: spectator and FPS camera without overturning" date="28 Nov 2012"/>

const itemStyles = {
    date: {
        color: '#b1b1b1',
        display: 'inline-block',
        width: '130px'
    }
}

const Item = injectSheet(itemStyles)(({ link, description, classes }) => (
    <Paragraph>
        <span className={classes.date}>{description.date}</span>
        <Link to={link}><strong>{description.title}</strong></Link>
    </Paragraph>
));

const Page = () => (
    <div>
        <PageTitle text="Articles"/>

        <Item link="/lua-intf" description={LuaIntfPage}/>
        <Item link="/chaiscript-recipes" description={ChaiscriptRecipesPage}/>
        <Item link="/nsf-devlog2" description={NsfDevlog2Page}/>
        <Item link="/nsf-devlog" description={NsfDevlog1Page}/>
        <Item link="/clion-multiple-binaries" description={ClionBinariesPage}/>
        <Item link="/unity3d-pick-and-drag" description={UnityPickAndDragPage}/>
        <Item link="/unreal-platforms" description={UnrealPlatformsPage}/>
        <Item link="/windows-main" description={WindowsMainPage}/>
        <Item link="/nether-devlog" description={NetherDevlogPage}/>
        <Item link="/unity3d-array-of-custom-objects" description={UnityArrayOfObjectsPage}/>
        <Item link="/freeglut-fixed-window" description={FreeGlutPage}/>
        <Item link="/odd-cpp" description={OddCppPage}/>
        <Item link="/unity3d-spectator-camera" description={UnityCameraPage}/>
    </div>
)

export default Page;



